.home-store-div {
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  border-radius: 10px;
  margin: 10px;
  padding: 7px;
  vertical-align: top;
  text-align: left;
  transition: 0.5s;
  max-height: 450px;
  overflow: hidden;
}

.home-store-div:hover,
.home-store-div:active {
  transform: scale(1.04);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
  overflow: auto;
}

.poster-image {
  /* width: 90%; */
  max-height: 300px;
  max-width: 95%;
  text-align: right;
  display: block;
  margin: 0 auto;
  border-radius: 5px;
  margin-bottom: 7px;
}
